import React, { useState } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import "../assets/style.css";

const QuestionBox = ({number, total, question, options, correct, classNames, showButton, selected, playAgain}) => {
  const [classToApply, setClassToApply] = useState({classNames});
  const [clicked, setClicked] = useState(false);


  const checkAnswer = (choice, index) => {


      const spot = Number(index);
      const updatedClasses = ['','','',''];

      var correctSpot = 4;

        if (options[0] === correct) {
          correctSpot = 0;
        } else if (options[1] === correct) {
          correctSpot = 1;
        } else if (options[2] === correct) {
          correctSpot = 2;
        } else if (options[3] === correct) {
          correctSpot = 3;
        }

      if (choice === correct) {
        setClassToApply(updatedClasses[spot] = "correct");
      } else {
        setClassToApply(updatedClasses[spot] = "wrong");
        setClassToApply(updatedClasses[correctSpot] = "correct");
      }

      setClassToApply(updatedClasses);
  }


  const answered = () => {
      setClicked(true);
  }

  const nextQ = () => {
    setClassToApply(['','','','']);
    setClicked(false);
  }



  return (
    <div className="questionBlock">
        <div className="question">
          <h5>{number} / {total}</h5>
          <h4>{question}</h4></div>
        <div className="optionButton">
            {options.map((text, i) => (
              <button
                  key={[i]}
                  value={[i]}
                  className={classToApply[i]}
                  onClick={() => {
                    checkAnswer(text, [i]);
                    answered();
                    selected(text);
                    showButton();

                    }}
                  disabled={clicked}
                  onLoad={() => {
                    nextQ();
                    }}
              >
                {text}
              </button>
            ))}
        </div>
    </div>
  );

}

export default QuestionBox;
