import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';

const Popup = ({number, total, score, style, startQuiz, playAgain}) => {
  const begQuiz ={
    title: "Welcome to the Playbook Quiz!",
    text: "Select the correct route for each question.",
    buttonText: "Start the quiz",
  };

  const [popup, setPopup] = useState(begQuiz);

  const endQuiz ={
    title: "Thanks for taking the quiz!",
    buttonText: "Play again?",
  };

  useEffect(() => {
      popup.text2 = "You scored " + score + " / " + total;
    },
  );

  const updatePopup = () => {
    setPopup(endQuiz)
  }

  const restart = () => {
    setPopup(begQuiz)
  }


  return (
    <div className="popupContainer" style={style}>
      <div className="col-md-8 col-md-offset-2">
        <div className="popup">
            <h1>{popup.title}</h1>
            {number === total ? <p>{popup.text2}</p> : <p>{popup.text}</p>}

              <br />
            {number === total ?
              <button
                className="playButton"
                onClick={() => {
                  playAgain();
                  restart();
                }}
              >
                {popup.buttonText}
              </button> : <button
                className="playButton"
                onClick={() => {
                  startQuiz();
                  updatePopup();
                }}
              >
                {popup.buttonText}
              </button>}
            <br />
            <br />
        </div>
      </div>
    </div>
  );
}



export default Popup
